import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ToastController, LoadingController } from 'ionic-angular';
import { BackProvider } from '../../providers/back/back';

@IonicPage()
@Component({
  selector: 'page-notas-add',
  templateUrl: 'notas-add.html',
})
export class NotasAddPage {

  public addData: any = {}
  private id: string = this.navParams.get('id')
  private type: string = this.navParams.get('type')

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private toast: ToastController,
    private back: BackProvider,
    private load: LoadingController
  ) {
  }

  addNota(title: string, text: string) {
    
    if (!title) return this.toast.create({message: 'Ingresa un titulo', duration: 2000}).present()
    if (!text) return this.toast.create({message: 'Ingresa un text', duration: 2000}).present()

    const l = this.load.create({content: 'Agregando...'})
    l.present()

    this.back.addNota(title, text, this.type, this.id).subscribe(data => {l.dismiss(); this.viewCtrl.dismiss(data)}, err => {
      this.toast.create({message: 'No se pudo agregar tu nota...', duration: 2000}).present()
      l.dismiss()
    })

  }

}



